.taskList {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.taskList > .tasksFilter {
  margin-top: -37px;
  background-color: #de8d27;
  box-shadow: none;
  transition: margin-top 0.3s;
}

.taskList > .tasksFilter.visible {
  margin-top: 0;
  box-shadow: 0px -8px 10px 10px rgba(0, 0, 0, 0.5);
}

.taskList > .tasksFilter > .tasksFilterInner {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 622px;
  margin-left: auto;
  margin-right: auto;
}

.taskList > .tasksFilter > .tasksFilterInner > .taskFilterText {
  background-color: white;
  border: 1px solid black;
  margin: 5px;
  padding: 5px;
  border-radius: 9px;
  flex-grow: 1;
}

.taskList > .tasks {
  flex-grow: 1;
  overflow-y: auto;
  scrollbar-width: thin;
}

.taskList > .tasksButtons {
  background-color: #77a4d2;
}

.task {
  cursor: pointer;
  background-color: white;
  margin: 10px auto;
  padding: 5px;
  box-shadow: 2px 2px 10px 0px rgba(0, 0, 0, 0.5);
  display: flex;
  max-width: 600px;
}

.task > .title {
  flex-grow: 1;
}

.taskForm {
  background-color: blue;
  padding: 20px;
}

.taskForm .button:hover {
  background-color: #527aa2;
}

.task-state {
  width: 100px;
  margin-left: auto;
  margin-right: auto;
  padding: 2px;
  border: 1px solid black;
  border-radius: 5px;
}

.task-state.small {
  height: 15px;
  width: 15px;
}

.task-state.created {
  background-color: #d8d8d8;
  border-color: #a2a2a2;
}

.task-state.in-backlog {
  background-color: #ef97ff;
  border-color: #e65fff;
}

.task-state.active {
  background-color: #5ae1ff;
  border-color: #0ccffb;
}

.task-state.finished {
  background-color: #76ff6d;
  border-color: #0dce00;
}

.task-state.abandoned {
  border-color: #616161;
  background-color: #616161;
  color: #c0c0c0;
}

.task-state-closed {
  display: flex;
  justify-content: center;
}

.task-state-closed > * {
  margin: 0 5px;
}
