.button {
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  cursor: pointer;
  margin: 5px;
  padding: 5px;
  background-color: #204b77;
  text-decoration: none;
  color: #e0f2ff;
  height: 22px;
}

.button.wide {
  background-color: #527aa2;
  min-width: 100px;
}

.button.light {
  background-color: #527aa2;
}

.button > img {
  max-width: 20px;
}
